
<template>
  <div>
    <div class="d-flex justify-content-between mx-5 mb-3">
      <div class="text-center bg-black p-2 cursor-pointer" style="border: 1px solid" @click="() => {}">
        <h4>Kết quả của T1FS</h4><hr/>
        <small>{{result.corrects}}/{{result.total}} Bản ghi</small>
      </div>
       <feather-icon icon="ArrowRightIcon" size="3x" class="my-auto"/>
      <div class="text-center bg-black p-2 cursor-pointer" style="border: 1px solid" @click="danh_gia_sai_so = true">
        <h4>Đánh giá sai số</h4><hr/><small>Click to run</small>
      </div>
      <feather-icon icon="ArrowRightIcon" size="3x" class="my-auto"/>
      <div class="text-center bg-black p-2 cursor-pointer" style="border: 1px solid" @click="dieu_chinh_fm()">
        <h4>Điều chỉnh Fm(c-)</h4><hr/><small>Click to run</small>
      </div>
      <feather-icon icon="ArrowRightIcon" size="3x" class="my-auto"/>
      <div class="text-center bg-black p-2 cursor-pointer" style="border: 1px solid" @click="predict()">
        <h4>Suy diễn trên HAT2FS</h4><hr/><small>Click to run</small>
      </div>
      <feather-icon icon="ArrowRightIcon" size="3x" class="my-auto"/>
      <div class="text-center bg-black p-2 cursor-pointer" style="border: 1px solid" @click="() => {}">
        <h4 class="pt-2">Kết thúc</h4>
      </div>
    </div>

     <div class="row">
      <div class="col-5" v-if="danh_gia_sai_so">
        <div class="d-flex justify-content-between">
          <h4 class="mb-1">Đánh giá sai số</h4>
        </div>
        <div class="rounded my-shadow table-50-vh"> 
          <b-table-simple no-border-collapse sticky-header fixed bordered responsive striped class="rounded mb-0 text-center">
            <b-thead>
              <b-tr class="cursor-pointer">
                <b-th>ID</b-th>
                <b-th>Thuộc tính</b-th>
                <b-th>Cụm sai</b-th>
                <b-th>μFalse</b-th>
                <b-th>Cụm đúng</b-th>
                <b-th>μTrue</b-th>
              </b-tr>
            </b-thead>
            <b-tbody>
              <b-tr v-for="datum, index in edit" :key="index">
                <b-td>{{index + 1}}</b-td>
                <b-td v-for="p, id in datum" :key="`${index}_x_${id}`">{{id == 2 || id == 4 ? formatMoney(p,4) : p}}</b-td>
              </b-tr>
            </b-tbody>
          </b-table-simple>
        </div>
      </div>

      <div class="col-7" v-if="start_edit">
        <div class="d-flex justify-content-between">
          <h4>Điều chỉnh Fm(c-)</h4>
        </div>
        <div class="my-scroll">
          <div v-for="item, index in aaaa" :key="`y${index}`">
            <div v-if="item=='editing'"  class="m-1 p-1 my-border my-shadow">Editting {{format_edit_record(edit[index])}}</div>
            <div v-else-if="item=='end'" class="text-success text-center">
              <h4>-------- Hoàn Thành --------</h4>
            </div>
            <div v-else :class="item.status=='edited' ? 'm-1 p-1 border-success shadow-success' : 'm-1 p-1 border-error shadow-error'">
              {{index + 1}}. Bản ghi: {{format_edit_record(item.editRecord)}}, Suy diễn trên tập training: {{item.old_corrects}} -> {{item.new_corrects}}, <span :class="item.status=='edited'?'text-success':'text-danger'">Status: {{item.status}}</span></div>
          </div>
          <div ref="scrollToMe" class="text-right mt-5">.</div>
        </div>
      </div>
     </div>

      <b-modal id="accept" hide-footer no-close-on-backdrop centered title="Xác nhận" size="lg" ref="my-modal">
        <div class="p-3">
          <h3>Kết quả cho tập traning với hệ logic mờ loại 1:</h3>
          <h3 class="text-center">{{result.corrects}}/{{result.total}} bản ghi</h3>
          <br/><hr/><br/>
          <h3>Chương trình đánh giá và điều chỉnh {{edit.length}} bản ghi đánh giá sai số, điều này có thể mất thời gian. Bạn xác nhận chứ?</h3>
        </div>
        <div class="w-full text-center">
          <b-button class="my-2 text-center" variant="success" @click="ok_edit_fmc()">Xác nhận</b-button>
          <b-button class="my-2 text-center ml-3" variant="info" @click="$bvModal.hide('accept')">Hủy bỏ</b-button>
        </div>
      </b-modal>

      <b-modal id="ha_predict_result" hide-footer no-close-on-backdrop centered size="lg" title="Kết Quả Hệ Logic Mờ Loại 2"  ref="my-modal">
        <div class="d-flex justify-content-between m-3"> 
          <div class="my-auto">
            <h2>Dữ liệu<br/><br/>Training</h2>
          </div>
          <div class="text-center bg-black p-2 cursor-pointer" style="border: 1px solid">
            <h4>T1FS</h4>
            <hr/>
            <p>Chính xác: {{result.corrects}}</p>
            <p>Tất cả: {{result.total}}</p>
            <p>Độ chính xác: {{formatMoney(result.corrects/result.total*100)}}%</p>
          </div>
          <feather-icon icon="ArrowRightIcon" size="3x" class="my-auto"/>
          <div class="text-center bg-black p-2 cursor-pointer" style="border: 1px solid" v-if="predict_train">
            <h4>HAT2FS</h4>
            <hr/>
            <p>Chính xác: {{predict_train.corrects}}</p>
            <p>Tất cả: {{predict_train.total}}</p>
            <p>Độ chính xác: {{formatMoney(predict_train.corrects/predict_train.total*100)}}%</p>
          </div>
        </div>
        <div class="d-flex justify-content-between m-3">
          <div class="my-auto">
            <h2>Dữ liệu<br/><br/>Testing</h2>
          </div>

          <div class="text-center bg-black p-2 cursor-pointer" style="border: 1px solid" v-if="predict_train">
            <h4>T1FS</h4>
            <hr/>
            <p>Chính xác: {{result_test.corrects}}</p>
            <p>Tất cả: {{result_test.total}}</p>
            <p>Độ chính xác: {{formatMoney(result_test.corrects/result_test.total*100)}}%</p>
          </div>
          <feather-icon icon="ArrowRightIcon" size="3x" class="my-auto"/>
          <div class="text-center bg-black p-2 cursor-pointer" style="border: 1px solid" v-if="predict_test">
            <h4>HAT2FS</h4>
            <hr/>
            <p>Chính xác: {{predict_test.corrects}}</p>
            <p>Tất cả: {{predict_test.total}}</p>
            <p>Độ chính xác: {{formatMoney(predict_test.corrects/predict_test.total*100)}}%</p>
          </div>
        </div>
      </b-modal>
  </div>
</template>

<script>
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import {
  BTableSimple,
  BThead,
  BTr,
  BTh,
  BTd,
  BTbody,
  BTfoot,
  BButton,
  BModal, BFormInput,
  VBModal, BInputGroup
} from "bootstrap-vue";
import Ripple from "vue-ripple-directive";
import axios from "@axios";
import { formatMoney } from '@core/utils/filter'

export default {
   components: {
    BTableSimple, Loading,
    BThead, BInputGroup, BFormInput,
    BTr,
    BTh,
    BTd,
    BTbody,
    BTfoot,
    BButton,
    BModal,
    VBModal
  },

  directives: {
    Ripple,
  },
  data() {
    return {
      edit: [],
      type_of_data: '',
      danh_gia_sai_so: false,
      start_edit: false,
      result: {},
      result_test: {},
      lastest_num_corrects: 0,
      fcm_change_result: [],
      predict_train: null,
      predict_test: null,
    }
  },

  computed: {
    aaaa(){
      if(this.fcm_change_result.length == this.edit.length) return [...this.fcm_change_result, 'end']
      return [...this.fcm_change_result, 'editing']
    }
  },

  methods: {
    format_edit_record(edit_record){
      return edit_record.map((it, i) => {
        if(i == 2 || i == 4) return parseFloat(formatMoney(it, 4))
        return it
      })
      
    },
    formatMoney,
    dieu_chinh_fm(){
      this.$bvModal.show("accept")
    },
    
    async ok_edit_fmc(){
      function sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
      }
      this.danh_gia_sai_so = true
      this.start_edit = true
      this.fcm_change_result = []
      this.$bvModal.hide("accept")
      let data = []
      let index = 0
      while(!data[index] && index < this.edit.length){
        let record = this.edit[index]
        const res = await axios.get(`https://hais2.herokuapp.com/ha_change_fmc/${this.type_of_data}?old_num_correct=${this.lastest_num_corrects}&editRecord=${JSON.stringify(record)}`)
        this.fcm_change_result.push(res.data)
        if(res.data.new_corrects > this.lastest_num_corrects) this.lastest_num_corrects = res.data.new_corrects
        index += 1
        const el = this.$refs.scrollToMe;
        el.scrollIntoView({behavior: 'smooth'});
        // await sleep(1000);
      }
    },

    predict(){
      let loader = this.$loading.show()
      axios.get(`https://hais2.herokuapp.com/ha_predict/${this.type_of_data}/test`).then(res => {
        this.predict_test = res.data
        this.$bvModal.show('ha_predict_result')
        loader.hide()
        axios.get(`https://hais2.herokuapp.com/ha_predict/${this.type_of_data}/train`).then(res => {
          this.predict_train = res.data
        })
      })
    }

  },
  mounted(){
    this.type_of_data = localStorage.getItem('type_of_data')
    if(!this.type_of_data) {
      this.$router.push('/')
      return
    }
    
    axios.get(`https://hais2.herokuapp.com/getItem/${this.type_of_data}_edit_fmc_train`).then(res => {
      this.edit = JSON.parse(res.data)
    })

    axios.get(`https://hais2.herokuapp.com/predict/${this.type_of_data}/train`).then(res => {
        this.result = res.data
        this.lastest_num_corrects = res.data.corrects
    })

    axios.get(`https://hais2.herokuapp.com/predict/${this.type_of_data}/test`).then(res => {
        this.result_test = res.data
    })

  }
}
</script>
<style scoped>
  .my-scroll {
    overflow-y: scroll;
    height: 51vh;
  }
</style>